import {createSlice} from "@reduxjs/toolkit";

//  
type Question = {
    id: string;
    name: string;
    percentage: number;
};

type Indicator = {
    id: string;
    name: string;
    percentage: number;
    questions: Question[];
};

type Competency = {
    id: string;
    name: string;
    percentage: number;
    indicators: Indicator[];
};

//    
const initialState: Competency[] = [
    {
        id: "1",
        name: "",
        percentage: 70,
        indicators: [
            {
                id: "1-1",
                name: " ",
                percentage: 80,
                questions: [
                    {id: "1-1-1", name: "   ?", percentage: 90},
                    {id: "1-1-2", name: "   ?", percentage: 70},
                ],
            },
            {
                id: "1-2",
                name: " ",
                percentage: 60,
                questions: [
                    {id: "1-2-1", name: "     ?", percentage: 60},
                ],
            },
        ],
    },
];

const appSlice = createSlice({
    name: "app",
    initialState,
    reducers: {
        //   ,   
        noop: (state) => state
    }
});

export const {noop} = appSlice.actions;

export default appSlice.reducer;